/// <reference path="index.d.ts"/>

let request = require("request-promise");

/**
 * Wattwin widget to display a wizard on external pages
 */
export class WizardWidget {
  private wizard: Wattwin.WizardWidget.Wizard;
  private params: Wattwin.WizardWidget.WidgetParams;
  private iframe: HTMLIFrameElement;
  private apiUrl: string;
  private appUrl: string;

  constructor(params: Wattwin.WizardWidget.WidgetParams) {
    if (params && params.accessToken && params.wizardCode) {
      this.params = params;
      window.addEventListener("message", (event: MessageEvent) =>
        this.manageListeners(event)
      );
    } else {
      console.error("Access Token and wizardCode are mandatory");
    }

    this.setDomains(this.params.environment);
  }

  /**
   * Handle the event given by the app and dispatch it
   * @param event Message event from the app
   */
  private manageListeners(event: MessageEvent): void {
    switch (event.data.event) {
      case "wizard.ready":
        this.iframe.style.display = "block";
        break;
    }
    const eventToDispatch = new CustomEvent(event.data.event, {
      detail: event.data.data
    });
    window.dispatchEvent(eventToDispatch);
  }

  /**
   * Set the domain url of the api and app page from the environment code
   * @param environment code of the environment
   */
  private setDomains(environment: "local" | "int" | "test" | "prod"): void {
    if (!environment || environment === "prod") {
      this.apiUrl = `https://pro-api.wattwin.com`;
      this.appUrl = `https://admin.wattwin.com`;
    } else if (environment && environment !== "local") {
      this.apiUrl = `https://${environment}-api.wattwin.com`;
      this.appUrl = `https://${environment}-admin.wattwin.com`;
    } else {
      // this.apiUrl = `http://localhost:3000`;
      this.apiUrl = `https://int-api.wattwin.com`;
      this.appUrl = `http://localhost:3010`;
    }
  }

  /**
   * Create an iframe with Wattwin App and load the wizard
   * @param containerId html div to attach the iframe
   */
  public async initWidget(containerId: string): Promise<void> {
    this.params.containerId = containerId;

    try {
      this.wizard = await this.getWizardByCode(this.params.wizardCode);
    } catch (error) {
      const eventToDispatch = new CustomEvent("widget.error", {
        detail: `Error: wizard not found with that code ${this.params.wizardCode}`
      });
      window.dispatchEvent(eventToDispatch);
      return null;
    }

    if (this.params.clientApplication) {
      try {
        const ca = await this.saveApplication();
        this.params.locatorCode = ca.locatorCode;
      } catch (error) {
        const eventToDispatch = new CustomEvent("widget.error", {
          detail: `Error at loading the application`
        });
        window.dispatchEvent(eventToDispatch);
        return null;
      }
    }

    var iframe: HTMLIFrameElement = document.createElement("iframe");
    iframe.width = "100%";
    iframe.height = "100%";
    iframe.setAttribute(
      "style",
      "position: absolute; top: 0; left: 0; border: none; display: none;"
    );
    iframe.src = encodeURI(this.createWattwinUrl());
    this.iframe = iframe;

    document.getElementById(this.params.containerId).appendChild(iframe);
  }

  /**
   * Build and return iFrame URL
   */
  private createWattwinUrl(): string {
    let widgetParams: string = btoa(JSON.stringify(this.params));
    return `${this.appUrl}/?widgetParams=${widgetParams}#/app/wizard/${this.wizard.id}`;
  }

  /**
   * Send a message to the application
   * @param event Event name
   * @param data Event data to send
   */
  private sendMessageToIframe(event: string, data: any): void {
    this.iframe.contentWindow.postMessage({ event: event, data: data }, "*");
  }

  /**
   * Retrieves the wizard by its unique code
   * @param wizardCode Code of the wizard being invoqued
   * @returns A promise with wizard instance
   */
  private getWizardByCode(
    wizardCode: string
  ): Promise<Wattwin.WizardWidget.Wizard> {
    return request({
      headers: {
        Accept: "application/json",
        "Accept-Charset": "utf-8",
        "X-Access-Token": this.params.accessToken
      },
      qs: {
        filter: {
          fields: ["id", "name", "code"]
        }
      },
      method: "GET",
      url: `${this.apiUrl}/api/Wizards/findByCode/${wizardCode}`,
      json: true
    });
  }

  /**
   * Save the application data given from widget params
   * @returns A promise with the saved client application
   */
  private saveApplication(): Promise<Wattwin.WizardWidget.Application> {
    if (!this.params.clientApplication.wizardId)
      this.params.clientApplication.wizardId = this.wizard.id;
    return request({
      headers: {
        Accept: "application/json",
        "Accept-Charset": "utf-8",
        "X-Access-Token": this.params.accessToken
      },
      body: this.params.clientApplication,
      method: "POST",
      url: `${this.apiUrl}/api/Wizards/saveApplication`,
      json: true
    });
  }
}
